import * as React from "react";
import * as PropTypes from "prop-types";

import NamespaceWrapper, { NamespaceWrapperProps } from "../utils/NamespaceWrapper";

export interface BreadcrumbProps extends NamespaceWrapperProps {
  /**
   * Set a class for the badge, prefer modifiers
   */
  className?: string;
}

export default class Breadcrumb extends NamespaceWrapper<BreadcrumbProps, {}> {
  public static displayName = "Breadcrumb";

  public static propTypes = {//
    // Modifiers
    "className": PropTypes.string, //
  };

  public render() {
    const className = this.classes("Breadcrumb", this.props.className != null ? this.props.className : "");

    return <div className={className} role="navigation">
      <ol className={this.classes("Breadcrumb__items")}>
        {this.props.children}
      </ol>
    </div>;
  }
}

export interface BreadcrumbItemProps extends NamespaceWrapperProps {
  /**
   * If set to true, renders `span` instead of `a`
   */
  active?: boolean;
  /**
   * `href` attribute for the inner `a` element
   */
  href?: string;
  /**
   * `title` attribute for the inner `a` element
   */
  title?: any;
  /**
   * `target` attribute for the inner `a` element
   */
  target?: string;

  /**
   * Add ability to add more classes
   */
  className?: string;
}

export class BreadcrumbItem extends NamespaceWrapper<BreadcrumbItemProps> {
  render() {
    const {namespace, active, href, title, target, className, ...props} = this.props;

    const linkProps = {href, title, target};

    return <li className={this.classes("Breadcrumb__item", active ? "is-current" : "", className)}>
      {active ?
        <span {...props} /> :
        <a {...props} {...linkProps} />
      }
    </li>;
  }
}
